define('syllabus/router', ['exports', 'ember', 'syllabus/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('exercises', { path: '/' }, function () {
      this.route('html-css', function () {
        this.route('1');
        this.route('2');
        this.route('3');
        this.route('4');
        this.route('5');
        this.route('6');
      });
      this.route('javascript', function () {
        this.route('1');
        this.route('2');
        this.route('3');
        this.route('4');
        this.route('5');
        this.route('6');
      });
      this.route('deploy', function () {});
      this.route('bootstrap', function () {
        this.route('1');
        this.route('2');
        this.route('3');
        this.route('4');
        this.route('5');
        this.route('6');
        this.route('7');
      });
    });
    this.route('result', { path: 'result' }, function () {
      this.route('html-css', function () {
        this.route('1');
        this.route('2');
        this.route('3');
        this.route('4');
        this.route('5');
        this.route('6');
      });
      this.route('javascript', function () {
        this.route('1');
        this.route('2');
        this.route('3');
        this.route('4');
        this.route('5');
        this.route('6');
      });
      this.route('bootstrap', function () {
        this.route('1');
        this.route('2');
        this.route('3');
        this.route('4');
        this.route('5');
        this.route('6');
        this.route('7');
      });
      this.route('deploy');
    });
    this.route('links', { path: 'links' });
    this.route('extra-material');
  });

  exports['default'] = Router;

});